 hidden;\n  color: #fff;\n  z-index: 2;\n  right: 10px;\n}\n.game-text .game-name {\n  font-size: 16px;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  width: 7em;\n}\n.game-num {\n  color: #ccc;\n}\n.game-des {\n  margin: 5px 0 16px;\n  display: none\n}\n.game-list ul {\n  margin-left: -12px;\n  height: 100%;\n}\n.game-list li:hover:before {\n  transform: translateY(0);\n  background: linear-gradient(rgba(0, 0, 0, .5) 40px, rgba(0, 0, 0, .9))\n}\n.game-list li:hover .game-des {\n  display: block;\n  display: -webkit-box;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n  word-break: break-all;\n  overflow: hidden;\n}\n.game-list li:hover .game-name {\n  width: 100%;\n}\n.game-box .text-des {\n  display: -webkit-box;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n  word-break: break-all;\n  white-space: normal;\n  height: 40px;\n  margin: 0 0 10px;\n}\n.module-game .module-full .text-des {\n  margin: 3px 0 10px;\n  font-size: 12px;\n  color: #fff;\n}\n.tag-box {\n}\n.tag {\n  height: 20px;\n  line-height: 20px;\n  display: inline-block;\n  padding: 0 4px;\n  color: #fff;\n}\n.tag-c1 {\n  background: #0f58ff;\n}\n.tag-c2 {\n  background: #00c288;\n}\n.tag-c3 {\n  background: #2dc0f6;\n}\n.tag-c4 {\n  background: #ffa63d;\n}\n.tag-c5 {\n  background: #ff6082;\n}\n.ico-start {\n  background-position: -60px -390px;\n}\n.ico-down {\n  background-position: -120px -390px;\n}\n.niu-rank {\n  float: right;\n  width: 210px;\n  margin-top: -36px;\n  position: relative;\n  z-index: 2\n}\n.game-info {\n  position: relative;\n  z-index: 2;\n  height: 100%;\n}\n.game-rank-tit {\n  line-height: 36px;\n  font-size: 14px;\n  text-align: right;\n}\n.game-rank-tit a {\n  padding: 2px 5px;\n  border-radius: 20px;\n}\n.game-rank-tit .cur {\n  color: #fff;\n  background: rgba(0, 0, 0, .3);\n}\n.game-tab {\n  height: 452px;\n  background: rgba(255, 255, 255, .5);\n  overflow: hidden;\n}\n.game-tab-box {\n  position: relative;\n  padding: 0 0 32px;\n  display: none;\n}\n.game-played,\n.new-server-box {\n  height: 420px;\n  overflow: hidden;\n}\n.played-text {\n  height: 91px;\n  text-align: center;\n  padding: 55px 0 0;\n  border-bottom: solid 1px rgba(0, 0, 0, 0.1);\n}\n.game-played li {\n  height: 104px;\n  position: relative;\n  padding: 0 5px 0 120px;\n  border-bottom: solid 1px rgba(0, 0, 0, 0.1);\n}\n.game-played li:hover {\n  background: #fff;\n}\n.played-pic {\n  position: absolute;\n  left: 8px;\n  top: 11px;\n  width: 105px;\n  height: 86px;\n  overflow: hidden;\n}\n.played-pic .label-inv {\n  width: 20px;\n  height: 16px;\n  background: #ff3351;\n  line-height: 16px;\n  text-align: center;\n  color: #fff;\n  position: absolute;\n  top: 0;\n  left: 0;\n  font-size: 12px;\n}\n.game-played dt {\n  padding: 12px 0 0;\n  line-height: 28px;\n  margin: 0 10px 0 0;\n}\n.game-played dt a {\n  display: block;\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n  margin: 5px 0 0;\n  font-size: 14px;\n  transition: .3s\n}\n.game-played dt a:hover {\n  opacity: .8\n}\n.game-played dd {\n  font-size: 12px;\n}\n.played-pic img {\n  width: 100%;\n  height: 100%;\n}\n.server {\n  position: relative;\n  margin-top: -3px;\n}\n.server .select {\n  display: block;\n  line-height: 23px;\n  color: #666;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n.btn-r-game {\n  width: 84px;\n  height: 24px;\n  line-height: 24px;\n  color: #fff;\n  display: block;\n  text-align: center;\n  position: absolute;\n  bottom: 10px;\n  font-size: 12px;\n}\n.game-close {\n  width: 17px;\n  height: 17px;\n  position: absolute;\n  right: 2px;\n  top: 5px;\n  display: block;\n  line-height: 800;\n  overflow: hidden;\n  background-position: 0 -370px;\n  transition: .5s;\n}\n.game-close:hover {\n  transform: rotate(180deg);\n}\n.ico-arw-up,\n.ico-arw-down {\n  width: 20px;\n  height: 12px;\n  display: block;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  margin: -6px 0 0 -10px;\n  background-position: -40px -370px;\n}\n.ico-arw-down {\n  transform: rotateX(180deg);\n}\n.btn-game-wp {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  height: 32px;\n  line-height: 32px;\n  display: flex;\n}\n.btn-game-wp a {\n  display: block;\n  height: 100%;\n  position: relative;\n  text-align: center;\n  transition: .4s;\n  width: 50%;\n  flex: 1;\n}\n.btn-game-wp a:hover {\n  background: rgba(255, 255, 255, 1);\n}\n.btn-game-wp .btn-dis,\n.btn-game-wp .btn-dis:hover {\n  cursor: default;\n  background: none;\n}\n.btn-game-wp .btn-dis .ico-arw-up,\n.btn-game-wp .btn-dis .ico-arw-down {\n  filter: brightness(2)\n}\n.empty-record {\n  height: 100% !important\n}\n.empty-load {\n  padding: 70% 0 0;\n  text-align: center;\n  font-size: 14px;\n}\n.empty-load p {\n  margin: 0 0 10px;\n}\n.empty-load .btn-r-game {\n  position: relative;\n  display: inline-block;\n  bottom: auto !important;\n  margin: 5px 0 0;\n  width: 96px;\n  height: 28px;\n  line-height: 28px;\n}\n.game-rank-tab {\n  height: 36px;\n  line-height: 36px;\n}\n.game-rank-tab a {\n  float: left;\n  width: 50%;\n  text-align: center;\n  color: #666;\n  font-size: 14px;\n}\n.game-rank-tab .cur {\n  background: #fff;\n  color: #1a1a1a;\n}\n.game-rank-list {\n  display: none;\n  height: 455px;\n}\n.game-rank-list li {\n  height: 40px;\n  position: relative;\n}\n.game-rank-list li a {\n  height: 100%;\n  display: block;\n  overflow: hidden;\n}\n.game-rank-list .pic {\n  display: none;\n}\n.game-rank-list .show {\n  height: 95px;\n}\n.game-rank-list .show .pic {\n  display: block;\n  margin-left: -20px;\n}\n.game-rank-list .show .info {\n  position: absolute;\n  top: 0;\n  z-index: 2;\n  width: 100%;\n  color: #fff;\n  line-height: 24px;\n}\n.game-rank-list .show .num {\n  display: none\n}\n.game-info-name {\n  margin: 0 0 0 2px;\n  overflow: hidden;\n  width: 6em;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n}\n.show .game-info-name {\n  font-size: 14px;\n  margin: 0 0 0 8px;\n}